.main-logo {
    margin-top: 10%;
}

header {
    width: 100%;
    height: 100vh;
}

#about {
    background-color: #000;
    color: #fff !important;
    padding: 5%;
    position: relative;
    overflow: hidden;
    background: url('../../media/video.webm') no-repeat center center;
    
}

#about::before {
    content: "";
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    /* Dark overlay */
    z-index: 1;
}

#background-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 0;
}

#about .container {
    position: relative;
    z-index: 1;
    color: #fff;
}